<template>
  <div class="order-management">
    <h2></h2>
    <div v-if="loading" class="loading">...</div>
    <div v-else>
      <!--  -->
      <div class="order-cards">
        <div v-for="order in orders" :key="order.id" class="order-card">
          <h3>ID: {{ order.id }}</h3>
          <div class="order-info">
            <div class="user-info">
              <div>{{ order.username }}</div>
              <div class="email">{{ order.email }}</div>
            </div>
            <div class="order-items">
              <div v-for="item in order.items" :key="item.menu_item_id" class="order-item">
                <span class="item-name">{{ item.name }}</span>
                <div class="item-details">
                  <span>: {{ item.quantity }}</span>
                  <span>: {{ item.price }}</span>
                  <span>: {{ item.quantity * item.price }}</span>
                </div>
              </div>
            </div>
            <div class="price">: {{ order.total_price }}</div>
            <div class="status">
              <span :class="['status-tag', 'status-' + order.status]">
                {{ statusText[order.status] }}
              </span>
            </div>
            <div class="time-info">
              <div>{{ formatDate(order.created_at).date }}</div>
              <div class="time">{{ formatDate(order.created_at).time }}</div>
            </div>
            <div class="order-actions">
              <button 
                v-if="parseInt(order.status) === 0" 
                class="btn-confirm"
                @click="handleButtonClick(order)"
              ></button>
              <button 
                v-if="parseInt(order.status) === 1" 
                class="btn-complete"
                @click="handleButtonClick(order)"
              ></button>
              <button 
                v-if="parseInt(order.status) === 2" 
                class="btn-delete"
                @click="handleDeleteOrder(order)"
              ></button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="orders.length === 0 && !loading">
      <p class="empty-message"></p>
    </div>

    <!--  -->
    <div v-if="showDialog" class="dialog-overlay">
      <div class="dialog">
        <p>{{ dialogMessage }}</p>
        <button @click="closeDialog"></button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { ElMessage } from 'element-plus';

const API_URL = process.env.VUE_APP_API_URL;
console.log('Current API_URL:', API_URL);

export default {
  created() {
    // 
    if (window.caches) {
      caches.keys().then(names => {
        names.forEach(name => {
          caches.delete(name);
        });
      });
    }
  },
  data() {
    return {
      orders: [], // 
      showDialog: false,
      dialogMessage: '',
      lastActionTime: 0, // 
      loading: false,
      // 
      statusText: {
        0: '',
        1: '',
        2: '',
        3: ''
      }
    };
  },
  async mounted() {
    await this.fetchOrders(); // 
  },
  methods: {
    async fetchOrders() {
      try {
        this.loading = true;
        const token = localStorage.getItem('authToken');
        
        if (!token) {
          ElMessage.error('');
          return;
        }

        // 
        const timestamp = new Date().getTime();
        const response = await axios.get(`${API_URL}/canteen/orders?_t=${timestamp}`, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Cache-Control': 'no-cache',
            'Pragma': 'no-cache'
          }
        });

        if (response.data.orders) {
          this.orders = response.data.orders.map(order => ({
            ...order,
            lastActionTime: 0,
            items: order.items || [] //  items 
          }));
        }
      } catch (error) {
        console.error(':', error);
        if (error.response?.status === 403) {
          ElMessage.error('');
        } else {
          ElMessage.error(error.response?.data?.message || '');
        }
      } finally {
        this.loading = false;
      }
    },
    
    async handleButtonClick(order) {
      if (!this.canPerformAction(order)) {
        ElMessage.warning('');
        return;
      }

      try {
        let newStatus;
        const currentStatus = parseInt(order.status);
        switch (currentStatus) {
          case 0:  // pending
            newStatus = 1;  // confirmed
            break;
          case 1:  // confirmed
            newStatus = 2;  // completed
            break;
          default:
            ElMessage.error(` ${this.statusText[order.status]} `);
            return;
        }

        await this.updateOrderStatus(order, newStatus);
        // 
        await this.fetchOrders();
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      }
    },

    canPerformAction(order) {
      const now = Date.now();
      // 3
      if (!order.lastActionTime || now - order.lastActionTime > 3000) {
        return true;
      }
      return false;
    },

    async updateOrderStatus(order, status) {
      try {
        console.log('Updating order status:', { orderId: order.id, status: status });
        await axios.put(
          `${API_URL}/canteen/orders/${order.id}`, 
          { status }, 
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('authToken')}`,
              'Content-Type': 'application/json'
            }
          }
        );
        
        ElMessage.success(`${this.statusText[status]}`);
        order.lastActionTime = Date.now(); // 
        order.status = status; // 
      } catch (error) {
        console.error(':', error);
        ElMessage.error(error.response?.data?.message || '');
        throw error;
      }
    },

    async handleDeleteOrder(order) {
      try {
        if (!confirm('')) {
          return;
        }
        
        await axios.delete(`${API_URL}/canteen/orders/${order.id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('authToken')}`
          }
        });
        
        ElMessage.success('');
        // 
        this.orders = this.orders.filter(o => o.id !== order.id);
      } catch (error) {
        console.error(':', error);
        ElMessage.error(error.response?.data?.message || '');
      }
    },

    closeDialog() {
      this.showDialog = false;
      this.dialogMessage = '';
    },

    formatDate(dateString) {
      const date = new Date(dateString);
      return {
        date: date.toLocaleDateString('zh-CN', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit'
        }),
        time: date.toLocaleTimeString('zh-CN', {
          hour: '2-digit',
          minute: '2-digit'
        })
      };
    }
  }
};
</script>

