<template>
  <div class="submit-feedback">
    <h2></h2>

    <!--  -->
    <div class="feedback-history" v-if="feedbacks.length > 0">
      <h3></h3>
      <div v-for="feedback in feedbacks" :key="feedback.id" class="feedback-item">
        <div class="feedback-header">
          <span class="feedback-time">{{ formatTime(feedback.created_at) }}</span>
          <button class="delete-btn" @click="deleteFeedback(feedback)"></button>
        </div>
        <div class="feedback-content">
          <p><strong></strong>{{ feedback.message }}</p>
          <p v-if="feedback.admin_reply" class="admin-reply">
            <strong></strong>{{ feedback.admin_reply }}
          </p>
        </div>
      </div>
    </div>

    <!--  -->
    <textarea v-model="feedbackMessage" placeholder="" rows="4" required></textarea>

    <!--  -->
    <button @click="submitFeedback"></button>

    <!--  -->
    <div v-if="feedbackSubmitted" class="submit-success">
      <p></p>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
const BASE_URL = process.env.VUE_APP_BASE_URL;

export default {
  data() {
    return {
      feedbackMessage: '', // 
      feedbacks: [], // 
      feedbackSubmitted: false, // 
      message: {
        show: false,
        content: '',
        type: ''
      }
    };
  },
  mounted() {
    this.loadFeedbacks(); // 
  },
  methods: {
    getAuthHeaders() {
      const token = localStorage.getItem('authToken');
      return token ? { 'Authorization': `Bearer ${token}` } : {};
    },

    // 
    async loadFeedbacks() {
      const userId = localStorage.getItem('userId');
      const token = localStorage.getItem('authToken');
      
      if (!userId || !token) {
        this.showMessage('', 'error');
        return;
      }
      
      try {
        const response = await axios.get(`${BASE_URL}/api/canteen/feedbacks`, {
          params: { user_id: userId },
          headers: this.getAuthHeaders()
        });
        this.feedbacks = response.data.feedbacks; // 
      } catch (error) {
        console.error(':', error);
        this.showMessage('', 'error');
      }
    },

    // 
    async submitFeedback() {
      const userId = localStorage.getItem('userId');
      const token = localStorage.getItem('authToken');

      if (!userId || !token) {
        this.showMessage('', 'error');
        return;
      }

      if (!this.feedbackMessage.trim()) {
        this.showMessage('', 'error');
        return;
      }

      try {
        await axios.post(`${BASE_URL}/api/canteen/feedback`, {
          user_id: userId,
          message: this.feedbackMessage,
        }, {
          headers: this.getAuthHeaders()
        });

        this.showMessage('');
        this.feedbackMessage = ''; // 

        this.feedbackSubmitted = true; // 
        setTimeout(() => {
          this.feedbackSubmitted = false;
        }, 2000);

        // 
        await this.loadFeedbacks();
      } catch (error) {
        console.error(':', error);
        this.showMessage('', 'error');
      }
    },

    // 
    async deleteFeedback(feedback) {
      if (!confirm('')) return;

      const userId = localStorage.getItem('userId');
      const token = localStorage.getItem('authToken');
      
      if (!userId || !token) {
        this.showMessage('', 'error');
        return;
      }

      try {
        await axios.delete(`${BASE_URL}/api/canteen/feedback/${feedback.id}`, {
          headers: this.getAuthHeaders(),
          data: {
            user_id: userId,
            is_admin: localStorage.getItem('userRole') === 'admin'
          }
        });
        
        this.showMessage('');
        await this.loadFeedbacks(); // 
      } catch (error) {
        console.error(':', error);
        this.showMessage(error.response?.data?.message || '', 'error');
      }
    },

    // 
    showMessage(content, type = 'success') {
      this.message = {
        show: true,
        content,
        type
      };
      setTimeout(() => {
        this.message.show = false;
      }, 3000);
    },

    // 
    formatTime(time) {
      return new Date(time).toLocaleString();
    }
  },
};
</script>

<style scoped>
.submit-feedback {
  padding: 20px;
}

textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
}

button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

.feedback-item {
  margin-bottom: 20px;
  border: 1px solid #ddd;
  padding: 10px;
}

.feedback-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.feedback-time {
  color: #666;
  font-size: 0.9em;
}

.delete-btn {
  padding: 4px 8px;
  font-size: 12px;
  background-color: #ff5252;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.delete-btn:hover {
  background-color: #ff1744;
  transform: translateY(-1px);
}

.submit-success {
  background-color: #d4edda;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
  color: #155724;
}
</style>