<template>
  <div class="canteen-order">
    <h2></h2>

    <!--  -->
    <div v-if="menuItems.length > 0" class="menu-list">
      <div v-for="item in menuItems" :key="item.id" class="menu-item">
        <img v-if="item.image_url" 
             :src="item.blobUrl || ''" 
             @error="loadImage(item)" 
             alt="" />
        <div class="menu-item-info">
          <h3>{{ item.name }}</h3>
          <p class="description">{{ item.description }}</p>
          <p class="price">{{ item.price }}</p>
          <div class="quantity-controls">
            <button @click="decrementQuantity(item)">-</button>
            <span>{{ item.quantity }}</span>
            <button @click="incrementQuantity(item)">+</button>
          </div>
          <button @click="addToCart(item)" class="add-to-cart"></button>
        </div>
      </div>
    </div>

    <div v-else>
      <p></p>
    </div>

    <!--  -->
    <div v-if="cart.length > 0" class="cart">
      <h3></h3>
      <ul>
        <li v-for="(cartItem, index) in cart" :key="cartItem.id" class="cart-item">
          <span>{{ cartItem.name }} ({{ cartItem.quantity }}): {{ cartItem.price * cartItem.quantity }} </span>
          <button @click="removeFromCart(index)" class="remove-btn"></button>
        </li>
      </ul>
      <div class="cart-footer">
        <button @click="checkout" class="checkout-btn"></button>
      </div>
    </div>
    <div v-else>
      <p></p>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

const BASE_URL = process.env.VUE_APP_BASE_URL;

export default {
  data() {
    return {
      menuItems: [], // 
      cart: [], // 
      imageCache: new Map(), // 
    };
  },
  async mounted() {
    await this.fetchMenuItems(); // 
  },
  methods: {
    async fetchMenuItems() {
      try {
        const response = await axios.get(`${BASE_URL}/api/auth/canteen/menu`);
        this.menuItems = response.data.map(item => ({ ...item, quantity: 1 })); //  quantity 1
        // 
        for (const item of this.menuItems) {
          await this.loadImage(item);
        }
      } catch (error) {
        console.error(':', error);
      }
    },

    async loadImage(item) {
      if (!item.image_url) return;
      item.blobUrl = await this.getImageUrl(item.image_url);
    },

    addToCart(item) {
      // 
      const existingItem = this.cart.find(cartItem => cartItem.id === item.id);
      if (existingItem) {
        existingItem.quantity += item.quantity; // 
      } else {
        this.cart.push({ ...item });
      }
      item.quantity = 1; // 1
      alert(`${item.name} `);
    },

    removeFromCart(index) {
      this.cart.splice(index, 1); // 
      alert('');
    },

    async checkout() {
      const orders = this.cart.map(cartItem => ({
        menuItemId: cartItem.id, 
        quantity: cartItem.quantity,
        price: cartItem.price 
      }));

      if (orders.length === 0) {
        alert('');
        return;
      }

      try {
        console.log('Orders being sent:', orders);
        const response = await axios.post(
          `${BASE_URL}/api/auth/canteen/order`,
          { items: orders },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('authToken')}`,
            }
          }
        );

        alert(response.data.message || '');
        this.$router.push(`/order/${response.data.orderId}`);
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },

    async getImageUrl(path) {
      if (!path) return ''; 
      if (path.startsWith('http')) {
        return path;
      }
      
      // 
      if (this.imageCache.has(path)) {
        return this.imageCache.get(path);
      }

      try {
        const token = localStorage.getItem('authToken');
        const headers = { 'Authorization': `Bearer ${token}` };
        const uploadPath = path.startsWith('/uploads/') ? path : `/uploads/${path}`;
        const baseUrl = process.env.VUE_APP_BASE_URL || 'http://localhost:3000';
        const response = await fetch(`${baseUrl}${uploadPath}`, { headers });
        const blob = await response.blob();
        const blobUrl = URL.createObjectURL(blob);
        
        // 
        this.imageCache.set(path, blobUrl);
        
        return blobUrl;
      } catch (error) {
        console.error(':', error);
        return '';
      }
    },
    decrementQuantity(item) {
      if (item.quantity > 1) {
        item.quantity--;
      }
    },
    incrementQuantity(item) {
      item.quantity++;
    }
  }
};
</script>

