// adminAuth.js
export const adminAuth = {
  methods: {
    checkAdminAuth() {
      const userRole = localStorage.getItem('userRole');
      if (userRole !== 'admin' && userRole !== 'super_admin') {
        this.$router.push('/');
        return false;
      }
      return true;
    }
  },
  beforeMount() {
    // 
    this.checkAdminAuth();
  },
  beforeRouteEnter(to, from, next) {
    // 
    const userRole = localStorage.getItem('userRole');
    if (userRole !== 'admin' && userRole !== 'super_admin') {
      next('/');
    } else {
      next();
    }
  }
};
